import React, { Component } from "react";
import { Chip } from "@material-ui/core";
import "../App.css";
import { faTerminal, faLaptopCode , faDatabase, faProjectDiagram, faPencilRuler, faArchive, faFile } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
const skillsData = require('../data.json');

export default class SkillsToasts extends Component {
  render() {
    return (
      skillsData.skills.map((item, i) => {
        var icon;
        if (item.category === "Coding") {
          icon = <FontAwesomeIcon icon={faTerminal} style={{ color: "#8ec0ff" }} />;
        } else if (item.category === "Development") {
          icon = <FontAwesomeIcon icon={faLaptopCode} style={{ color: "#8ec0ff" }} />;
        } else if (item.category === "Research") {
          icon = <FontAwesomeIcon icon={faProjectDiagram} style={{ color: "#8ec0ff" }} />;
        } else if (item.category === "Design") {
          icon = <FontAwesomeIcon icon={faPencilRuler} style={{ color: "#8ec0ff" }} />;
        } else if (item.category === "Database") {
          icon = <FontAwesomeIcon icon={faDatabase} style={{ color: "#8ec0ff" }} />;
        } else if (item.category === "Writing") {
          icon = <FontAwesomeIcon icon={faArchive} style={{ color: "#8ec0ff" }} />;
        } else {
          icon = <FontAwesomeIcon icon={faFile} style={{ color: "#8ec0ff" }} />;
        }
        return (
          <Chip variant="outlined" size="small" icon={icon} style={{ padding: "10px", marginBottom: "5px", marginRight: "5px" }} label={item.skill + " | " + item.proficiency} />
        );
      })
    );
  }
}