import React, { Component } from "react";
import { Grid, Paper } from "@material-ui/core";
import "../App.css";
import Typography from '@material-ui/core/Typography';

const edData = require('../data.json');

export default class EducationTimeline extends Component {
  render() {
    return (
      edData.education.map((item, i) => {
        return (
          <Grid item xs={12} sm={12} md={4} lg={4} xl={4}>
            <Paper elevation={3} spacing={2} style={{ padding: "10px", borderRadius: "5px"}}>
              <Typography variant="h6" style={{color: "#8ec0ff" }} >{item.Degree}</Typography>
              <Typography variant="subtitle1" >{item.Specialization}</Typography>
              <Typography variant="subtitle1" >{item.Institute}</Typography>
              <Typography variant="subtitle1" >Batch of {item.Batch}</Typography>
            </Paper>
          </Grid>
        );
      })
    );
  }
};